<template>
  <el-form style="width:580px" :model="User" ref="User" :rules="regRules" label-width="80">
    <el-form-item label="学号：">
      <el-input type="text" style="width:480px" v-model="User.userId" size="large" disabled></el-input>
    </el-form-item>
    <el-form-item label="姓名：">
      <el-input type="text" style="width:480px" v-model="User.userName" size="large" disabled></el-input>
    </el-form-item>
    <el-form-item label="性别：">
      <el-input type="text" style="width:480px" v-model="User.userSex" size="large" disabled></el-input>
    </el-form-item>
    <el-form-item label="专业：">
      <el-input type="text" style="width:480px" v-model="User.userDept" size="large" disabled></el-input>
    </el-form-item>
    <el-form-item label="类型：">
      <el-input type="text" style="width:480px" v-model="User.userType" size="large" disabled></el-input>
    </el-form-item>
    <el-form-item label="邮箱：" prop="userEmail" :rules="[
        { required: true, message: '请输入邮箱地址', trigger: 'blur' },
        { type: 'email', message: '请输入正确的邮箱地址', trigger: 'blur,change' }
        ]">
        <el-input style="width:470px" v-model="User.userEmail" placeholder="邮箱"></el-input>
      </el-form-item>
    <el-form-item label="手机：" prop="userPhone">
      <el-input type="number" style="width:470px" v-model="User.userPhone" size="large"></el-input>
    </el-form-item>
    <el-form-item>
      <el-button style="width:140px" size="large" type="primary" @click.native.prevent="regSubmit">修改</el-button>
    </el-form-item>
  </el-form>
</template>

<script>
  export default {
    data() {
      var validateUserName = (rule, value, callback) => {
        if (value === '') {
          callback(new Error('请输入内容！'))
        } else {
          callback();
        }
      }
      return {
        User:{
          userName:'',
          userPhone:'',
          userId:'',
          userDept:'',
          userType:'',
          userSex:'',
          userEmail:''
        },
        regRules:{
          userPhone: [
            { required: true, validator: validateUserName, trigger: 'blur' },
            { min: 6, max: 11, message: '请输入正确的手机号码！', trigger: 'blur' }
          ]
        },
      }
    },
    created(){
      this.User = JSON.parse(sessionStorage.getItem('BIGDATA_PLATFORM'))
    },
    methods:{
      regSubmit(){
        var _this = this;
        this.$refs.User.validate((valid) => {
          if (valid) {
            this.$ajax.post('/api/modifyUserInfo',_this.User
              ).then(function(response){
              _this.callMsg(_this,response)
              if(response.data.code === 0){
                sessionStorage.setItem('BIGDATA_PLATFORM', JSON.stringify(_this.User))
              }
            }).catch(function(error){console.log(error)})
          } else {
            return false;
          }
        }) 
      }
    }
  }
</script>



